import {customElement} from '@here/hds-base';
import {html, LitElement} from 'lit';

import {styles} from './hds-overlay.css.js';

/**
 * @slot default Content to be on top of the overlay
 */
@customElement('hds-overlay')
export class Overlay extends LitElement {
  static styles = styles;
  static shadowRootOptions = {...LitElement.shadowRootOptions, delegatesFocus: true};

  connectedCallback() {
    super.connectedCallback();
    this.shadowRoot!.ownerDocument.body.classList.add('hds-noscroll');
  }

  disconnectedCallback() {
    super.disconnectedCallback();
    this.shadowRoot!.ownerDocument.body.classList.remove('hds-noscroll');
  }

  protected render() {
    return html`
      <div class="wrapper">
        <slot></slot>
      </div>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-overlay': Overlay;
  }
}
